import {React, useEffect, useState} from 'react'
import './feed.scss'
import firebase from 'firebase'


import Events from '../../components/events'
import Post from '../../components/post'

import photo from '../../assets/images/icons/gallery.svg'
import video from '../../assets/images/icons/youtube.svg'
import calendar from '../../assets/images/icons/calendar.svg'
import article from '../../assets/images/icons/content-writing.svg'
import { db } from '../../helpers/firebase'
import { useSelector } from 'react-redux'
import { selectUser } from '../../features/userSlice';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';

function Feed() {
    const [input, setInput] = useState('')
    const [post, setPost] = useState([])
    const user = useSelector(selectUser)

    useEffect(() => {
        db.collection("posts").orderBy('timestamp', 'desc').onSnapshot(snapshot => (
            setPost(
                snapshot.docs.map((doc) => ({
                        id: doc.id,
                        data: doc.data(),
                })))
        ))
    }, [])

    const sendPost = (e) => {
        e.preventDefault()

        db.collection('posts').add({
            name: user.displayName,
            desc: 'This is a LinkedIn clone',
            img: user.photoUrl ? user.photoUrl : <AccountCircleIcon/>,
            text: input,
            timestamp: firebase.firestore.FieldValue.serverTimestamp()
        })

        setInput('');
    }

    return (
        <div className='feed'>
            <div className="feed-top">
                <div className="feed-top-input-wrapper">
                {
                    (!user.photoUrl) ?  <AccountCircleIcon/> : <img src={user.photoUrl} alt="" className="feed-top-input-wrapper__img" />
                }
                    <form>
                        <input value={input} onChange = {e => setInput(e.target.value)} type="text" className="feed-top-input-wrapper__input" placeholder='Start a post'/>
                        <button
                        onClick = {sendPost}
                        className="feed-top-input-wrapper__btn"
                        type='submit'
                        >Send</button>
                    </form>
                </div>
                <div className="feed-top-wrapper">
                    <Events
                        title = 'Photo'
                        img = {photo}
                    />
                    <Events
                        title = 'Video'
                        img = {video}
                    />
                    <Events
                        title = 'Calendar'
                        img = {calendar}
                    />
                    <Events
                        title = 'Write article'
                        img = {article}
                    />
                </div>
            </div>

            <div className="feed-bottom">
                <>
                    {post.map(( {id,data: {name, desc, img, text } } ) => (
                        <Post
                             key = {id}
                             name = {name}
                             desc = {desc}
                             img = {img}
                             text = {text}
                        />
                    ))}
                </>
                <Post
                    text = 'Hey Whatsupp'
                    name = 'Saidabror Nizamkhujaev'
                    desc = 'This is a LinkedIn clone'
                    img = ''
                />
            </div>
        </div>
    )
}

export default Feed