import React, { useState, useEffect } from 'react'
import { Switch, Route, useRouteMatch } from 'react-router-dom'
import axios from 'axios';

import './message.scss'

import Search from '../../assets/images/icons/magnifier.svg'
import Settings from '../../assets/images/icons/settings.svg'
import Ava from '../../assets/images/ava.jpg'
import Gellary from '../../assets/images/icons/gallery.svg'
import Attech from '../../assets/images/icons/attach.svg'
import Gif from '../../assets/images/icons/gif.svg'
import VideoCamera from '../../assets/images/icons/camera.svg'

import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import EditIcon from '@material-ui/icons/Edit';
import Usermessage from '../../components/userMessage'
import Message1 from '../../components/message1';

function Message() {

    const { path, url } = useRouteMatch()

    let ownUser = window.location.pathname.split("/")[3]

    const [messageOwner, setMessageOwner] = useState({
        isFetched: false,
        data: [],
        error: null
    })

    useEffect(() => {
        axios.get('https://randomuser.me/api/?results=5')
            .then(function (response) {
                setMessageOwner({
                    isFetched: true,
                    data: response.data.results,
                    error: false
                })
            }).catch(function (error) {
                setMessageOwner({
                    isFetched: true,
                    data: [],
                    error: error
                })
            }).then(function () { })
    }, []);

    let i = 0
    let j = 0

    return (
        <div className='message container'>
            <div className="message-left">  
                <div className="user-block">
                    <div className="user-block-top">
                        <h4 className="user-block-top__title">Messaging</h4>
                        <div>
                            <MoreHorizIcon />
                            <EditIcon />
                        </div>
                    </div>
                    <div className="user-block-search">
                        <div className='user-div'>
                            <img src={Search} alt="" className="user-div__img" />
                            <input placeholder='Search messages' type="search" className="user-block-search__input" />
                            <img src={Settings} alt="" className="user-div__img" />
                        </div>
                    </div>
                    <div className="users-wrapper">
                        <Usermessage
                            url={`${url}`}
                            exact='exact'
                            img={Ava}
                            name='Saidabror'
                            date={`${j = j + 1} Jul`}
                            email='nizamkhujaev@gmail.com'
                        />

                        <>
                            {
                                messageOwner.isFetched ? (
                                    <div className="users-wrapper">
                                        {
                                            messageOwner.data.map((item, index) => (
                                                <Usermessage
                                                    url={`${url}/user/${i = i + 1}`}
                                                    img = {item.picture.medium}
                                                    name = {item.login.username}
                                                    date={`${j = j + 1} Jul`}
                                                    email={item.email}
                                                />
                                            ))
                                        }
                                    </div>
                                ) : <div className="loader-wrapper">
                                        <div className="loader"></div>
                                    </div>
                            }
                        </>
                    </div>
                </div>

                <div className="message-block">
                    <Switch>
                        <Route exact component={Message1} path={`${path}`}/>
                        <Route exact path={`${path}/user/1`}>
                            <h1>Test Rejimda</h1>
                        </Route>
                    </Switch>
                    <div className="texting-area">
                        <f  orm>
                        <div className="texting-area-top">
                                <textarea placeholder='Write a message' className="texting-area-top__text"></textarea>
                            {/* </form> */}
                        </div>
                        <div className="texting-area-bottom">
                            <div className="texting-area-bottom-left">
                                <input type="file" id="gallery" accept="image/*" />
                                <label htmlFor="gallery" className="texting-area-bottom-left__label">
                                    <img src={Gellary} alt="" className="texting-area-bottom-left__img" />
                                </label>

                                <input type="file" id="gallery" accept="application/pdf" />
                                <label htmlFor="gallery" className="texting-area-bottom-left__label">
                                    <img src={Attech} alt="" className="texting-area-bottom-left__img" />
                                </label>

                                <input type="file" id="gallery" accept="image/gif" />
                                <label htmlFor="gallery" className="texting-area-bottom-left__label">
                                    <img src={Gif} alt="" className="texting-area-bottom-left__img" />
                                </label>

                                <input type="file" id="gallery"/>
                                <label htmlFor="gallery" className="texting-area-bottom-left__label">
                                    <img src={VideoCamera} alt="" className="texting-area-bottom-left__img" />
                                </label>

                            </div>

                            <div className="texting-area-bottom-right">
                                
                                {
                                    ownUser == 1 ? <button type='submit' className="texting-area-bottom-right__btn">send</button>
                                    : ownUser >= 2 ? <button type='submit' className="texting-area-bottom-right__btn">SeNd</button>
                                    : <button type='submit' className="texting-area-bottom-right__btn">Send</button>
                                }

                                <MoreHorizIcon/>
                            </div>
                        </div>
                    </f>
                    </div>
                </div>

            </div>

            <div className="message-right"></div>
        </div>
    )
}

export default Message
