import React, {useState} from 'react'
import './login.scss'
import logo from '../../assets/images/linkedin.svg'
import { Helmet } from 'react-helmet'
import { auth } from '../../helpers/firebase'
import { useDispatch } from 'react-redux'
import { login } from '../../features/userSlice'
 
function Login() {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [name, setName] = useState('');
    const [profilePic, setProfilePic] = useState('');
    const dispatch = useDispatch()
    
    
    const loginToApp = (e) => {
        e.preventDefault()

        auth.signInWithEmailAndPassword(email, password)
        .then(userAuth => {
            console.log(userAuth)
            dispatch(login({
                email: userAuth.user.email,
                uid: userAuth.user.uid,
                profileUrl: userAuth.user.photoURL,
                displayName: userAuth.user.displayName
            }))
        }).catch(err => alert(err))
    }

    const register = () => {
        if(!name) {
            alert('Please enter Your Full Name')
        }

        auth.createUserWithEmailAndPassword(email,password)
        .then((userAuth) => {
            userAuth.user.updateProfile({
                displayName: name,
                photoURL: profilePic
            })
            .then(() => {
                dispatch(login({
                    email: userAuth.user.email,
                    uid: userAuth.user.uid,
                    displayName: name,
                    photoURL: profilePic
                }))
            })
        }).catch(err => alert(err))
    }

    return (
        <div className='login'>

            <Helmet>
                <title>Login | LinkedIn Clone</title>
            </Helmet>

            <div className="login-wrapper">
               Linked <img src={logo} alt="" />
            </div>
            <form className='form'>

                <input
                    type="text"
                    placeholder='Full name (required if registering)'
                    required
                    className="form__input"
                    value={name}
                    onChange = {(e) => setName(e.target.value)}
                />

                <input
                    type="text"
                    placeholder='Profile pic URL(optional)'
                    className="form__input"
                    value={profilePic}
                    onChange = {(e) => setProfilePic(e.target.value)}
                />

                <input
                    type="email"
                    placeholder='Email'
                    className="form__input"
                    value={email}
                    onChange = {(e) => setEmail(e.target.value)}
                />

                <input
                    type="password"
                    placeholder='Password'
                    className="form__input"
                    value={password}
                    onChange = {(e) => setPassword(e.target.value)}
                />

                <button className="form__btn" onClick={loginToApp} type='submit'>Sign In</button>
            </form>
            <h4 className="login__title">
                Not a member? <span onClick={register}>Register Now!</span>
            </h4>

            <h5 className="login__subtitle">
                LinkedIn Clone By Nizamkhujaev Saidabror
            </h5>
        </div>
    )
}

export default Login
