import React from 'react'
import { NavLink } from 'react-router-dom'
import './userMesssage.scss'

function Usermessage({url,img, name, date,email, exact}) {
    return (
        <NavLink exact={exact} to={url} className='user-message' activeClassName='selected-user-message'>
            <div className="user-message-wrapper">
                <div className="useer-message-wrapper">
                    <img src={img} alt="" className="user-message__img" />
                </div>
                <div className='user-div'>
                    <h4 className="user-message-wrapper__name">{name}</h4>
                    <p className="user-message-wrapper__email">{email}</p>
                </div>
                    <span>{date}</span>
            </div>
        </NavLink>
    )
}

export default Usermessage
