import React, { useState } from 'react'
import './post.scss'
import Events from '../events'
import { Link } from 'react-router-dom'

import Comment from '../../assets/images/icons/comment.svg'
import Share from '../../assets/images/icons/share.svg'
import Send from '../../assets/images/icons/send.svg'
import Ava from '../../assets/images/icons/user.svg'
import LikeIcon from '../../assets/images/icons/like.svg'
import Liked from '../../assets/images/icons/liked.svg'


function Post({ img, name, desc, text }) {

    const [like, setLike] = useState(LikeIcon)

    return (
        <div className='post'>
            <div className="post-top">
                <img src={img ? img : Ava} alt="" className="post-top__img" />
                <div className="post-top-right">
                    <Link to={`${name}`.replace(/\s/g, "")} className="post-top-right__title">{name}</Link>
                    <h4 className="post-top-right__subtitle">{desc}</h4>
                </div>
            </div>
            <h4 className="post__title">{text}</h4>
            <div className="post-bottom">
                <div className="events" onDoubleClick = {() => setLike(Liked)}>
                        <img src={like} alt="" className="events__img" />
                        <h3 className="events__title">Like</h3>
                    </div>
                <Events
                    img={Comment}
                    title='Comment'
                />
                <Events
                    img={Share}
                    title='Share'
                />
                <Events
                    img={Send}
                    title='Send'
                />
            </div>
        </div>
    )
}

export default Post
