import React from 'react'
import './product.scss'

function Product({img,title}) {
    return (
        <div className='product'>
            <div className="product-top">
                <img src={img} alt="" className="product-top__img" />
            </div>
            <span className="product__title">{title}</span>         
        </div>
    )
}

export default Product
