import React, {useState} from 'react'
import './usercard.scss'


function UserCard({name, img,username,numb}) {
    const [pending, setPending] = useState('Connect')
    return (
        <div className='user-card'>
            <div className="user-card-top"></div>
            <img src={img} width='50' height='50' alt="" className="user-card__img" />
            <h4 className="user-card__title">{name}</h4>
            <h4 className="user-card__subtitle">{username}</h4>
            <h4 className="user-card__connected">{numb} mutual connections</h4>
            <button
                onClick={() => setPending('Pending...')}
                className={`user-card__btn`}
            >
                {pending}
            </button>
        </div>
    )
}

export default UserCard
