import React from 'react'
import './message.scss'

import Ava from '../../assets/images/ava.jpg'

function MessageCard() {
    return (
        <div className='message-card'>
            <img src={Ava} width='40' height='40' alt="" className="message-card__img" />

            <div className="message-card-wrapper">
                <h4 className="message-card-wrapper__name">
                    Saidabror Nizamkhujaev <span>{`${new Date().getHours()} : ${new Date().getMinutes()}`}</span>
                </h4>
                <p className="message-card-wrapper__text">
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Omnis tenetur dolor nobis pariatur soluta, consectetur, rerum ipsa autem
                </p>
            </div>
        </div>
    )
}

export default MessageCard
