import React, {useState, useEffect} from 'react'
import './networkusers.scss'
import {Link} from 'react-router-dom'
import axios from 'axios';

import UserCard from '../../components/user-card/UserCard'

function NetworkUsers({display,setDisplay}) {

    const [users, setUsers] = useState({
        isFetched: false,
        data: [],
        error: null
    })


    function getRandomInt(max) {
        return Math.ceil(Math.random() * max);
    }

    useEffect(() => {
        axios.get('https://randomuser.me/api/?results=60')
            .then(function (response) {
                setUsers({
                    isFetched: true,
                    data: response.data.results,
                    error: false
                })
            }).catch(function (error) {
                    setUsers({
                        isFetched: true,
                        data: [],
                        error: error
                    })
            }).then(function() {})
    },[]);


    return (
        <div className='network-users'>
            <div className="network-users-top">
                <h4 className="network-users-top__title">No pending invitations</h4>
                <Link to='/manage' className="network-users-top__subtitle">Manage</Link>
            </div>
            <div className="network-users-bottom">

            <div className="network-users-bottom-wrapper">
                <h4 className="network-users-bottom-wrapper__title">
                    Users you may know 
                </h4>

                <h4 className="network-users-bottom-wrapper__subtitle" onClick={() => setDisplay(!display)}>
                    See all
                </h4>
            </div>

            <div className="users-wrapper">
                {
                    users.isFetched ? (
                        <div className='users-wrapper-holder'>
                            {
                                users.data.map((item, index) => (
                                    <UserCard
                                        numb = {getRandomInt(18)}
                                        name = {item.name.first + item.name.last}
                                        img = {item.picture.medium}
                                        username = {item.login.username}
                                    />
                                ))
                            }
                        </div>
                    ) : <div className="loader-wrapper">    
                            <div className="loader"></div>
                        </div>
                }
            </div>

            </div>
        </div>
    )
}

export default NetworkUsers
