import React from 'react'
import './headeroption.scss'
 
function HeaderOption({img, title,triangle}) {
    return (
            <div className='header-option'>
                    {img}
                <h4 className={triangle ? 'header-option__title triangle' : 'header-option__title'} >{title}</h4>
            </div>
    )
}

export default HeaderOption
