import {React, useState} from 'react'
import { Link } from 'react-router-dom'
import AddIcon from '@material-ui/icons/Add';
import './options.scss'

function Options() {

    const [option, setOption] = useState(false)

    const fixedOption = () => {
        if(window.scrollY >= 700) {
            setOption(true)
        } else{
            setOption(false)
        }
    }
    
    window.addEventListener('scroll', fixedOption)

    return (
        <div className={option ? 'options changed' : 'options'}>
            <div className="options-top">
                <Link className="options-top__link" to='/group'>
                    Groups
                </Link>
                <div className="options-top-wrapper">
                    <Link className="options-top__link" to='/event'>
                        Events
                    </Link>
                        <AddIcon/>
                </div>
                <Link className="options-top__link" to='/hashtags'>
                    Followed Hashtags
                </Link>
            </div>

            <Link to='/more' className="options-bottom">
                Discover more
            </Link>
        </div>
    )
}

export default Options
