import React from 'react'
import './work.scss'
import ClearIcon from '@material-ui/icons/Clear';
import Product from '../../components/product/Product';
import { Link } from 'react-router-dom'

import Video from '../../assets/images/icons/video.svg'
import Insight from '../../assets/images/icons/insight.svg'
import Advertise from '../../assets/images/icons/advertise.svg'
import FindLeads from '../../assets/images/icons/find-leads.svg'
import Groups from '../../assets/images/icons/groups.svg'
import ProFinder from '../../assets/images/icons/profinder.svg'
import Salary from '../../assets/images/icons/salary.svg'
import AddIcon from '@material-ui/icons/Add';

function Work({show, setShow}) {
    return (
        <div className={`${show ? 'work show' : 'work'}`}>
            <div className="work-top">
                <ClearIcon onClick={() => setShow(!show)}/>
            </div>

            <div className="work-center">
                <h4 className="work-center__title">
                    Visit  More LinkedIn Products
                </h4>
                <div className="work-center-wrapper">
                    <div className="work-center-wrapper-top">
                        <Link to='/learning' className='work-learn'>
                            <Product
                                title='Learning'
                                img={Video}
                            />
                        </Link>

                        <Link to='/insight'>
                            <Product
                                title='Insights'
                                img={Insight}
                            />
                        </Link>

                        <Link to='/advertise'>
                            <Product
                                title='Advertise'
                                img={Advertise}
                            />
                        </Link>

                        <Link to='/findleads' className='work-link'>
                            <Product
                                title='Find Leads'
                                img={FindLeads}
                            />
                        </Link>
                    </div>

                    <div className="work-center-wrapper-bottom">
                        <Link to='/groups'>
                            <Product
                                title='Groups'
                                img={Groups}
                            />
                        </Link>

                        <Link to='/profinder'>
                            <Product
                                title='ProFinder'
                                img={ProFinder}
                            />
                        </Link>

                        <Link to='/salary' className='work-link'>
                            <Product
                                title='Salary'
                                img={Salary}
                            />
                        </Link>
                    </div>

                </div>
            </div>

            <div className="work-bottom">
                <h4 className="work-bottom__title">
                    LinkedIn Business Services
                </h4>
                <div className="work-bottom-center">
                    <Link to='/talent-solutions'>
                        <h5 className="work-bottom-center__title">
                            Talent Solutions
                        </h5>
                        <p className="work-bottom-center__subtitle">
                            Find attrack and recruit talent
                        </p>
                    </Link>

                    <Link to='/talent-solutions'>
                        <h5 className="work-bottom-center__title">
                            Talent Solutions
                        </h5>
                        <p className="work-bottom-center__subtitle">
                            Find attrack and recruit talent
                        </p>
                    </Link>

                    <Link to='/talent-solutions'>
                        <h5 className="work-bottom-center__title">
                            Talent Solutions
                        </h5>
                        <p className="work-bottom-center__subtitle">
                            Find attrack and recruit talent
                        </p>
                    </Link>

                    <Link to='/talent-solutions'>
                        <h5 className="work-bottom-center__title">
                            Talent Solutions
                        </h5>
                        <p className="work-bottom-center__subtitle">
                            Find attrack and recruit talent
                        </p>
                    </Link>

                    <Link to='/talent-solutions'>
                        <h5 className="work-bottom-center__title">
                            Talent Solutions
                        </h5>
                        <p className="work-bottom-center__subtitle">
                            Find attrack and recruit talent
                        </p>
                    </Link>
                </div>

                <div className="work-bottom-add">
                    <Link to='/add-page'>
                        Create a Company Page
                    </Link>
                    <AddIcon/>
                </div>
            </div>
        </div>
    )
}

export default Work
