import React, { useState } from 'react'
import './network.scss'
import { Helmet } from 'react-helmet'

import NetworkSidebar from '../../containers/network-saidbar'
import NetworkUsers from '../../containers/network-users'
import AllUsers from '../../components/allusers'

function Network({display, setDisplay}) {
    const [moreShow, setMoreShow] = useState(false)
    return (
        <div className='network container'>

            <Helmet>
                <title> Network | LinkedIn Clone</title>
            </Helmet>


            <div className="network-left">
                <NetworkSidebar
                    moreShow = {moreShow}
                    setMoreShow = {setMoreShow}
                />
            </div>

            <div className="network-right">
                    <AllUsers display={display} setDisplay={setDisplay} />
                    <NetworkUsers display={display} setDisplay={setDisplay}/>
            </div>
        </div>
    )
}

export default Network
