import React, {useState, useEffect} from 'react'
import axios from 'axios';
import './allusers.scss'

import CloseIcon from '@material-ui/icons/Close';
import UserCard from '../user-card/UserCard';

function AllUsers({display, setDisplay}) {

    const [allusers, setAllUsers] = useState({
        isFetched: false,
        data: [],
        error: null
    })

    function getRandomInt(max) {
        return Math.ceil(Math.random() * max);
    }

    useEffect(() => {
        axios.get('https://randomuser.me/api/?results=60')
            .then(function (response) {
                setAllUsers({
                    isFetched: true,
                    data: response.data.results,
                    error: false
                })
            }).catch(function (error) {
                setAllUsers({
                        isFetched: true,
                        data: [],
                        error: error
                    })
            }).then(function() {})
    },[]);
    
    return (
        <div className={`all-users ${display ? 'allsee' : ''}`}>
            <div className="all-users-top">
                <h4 className="all-users-top__title">Software Engineers you may know</h4>
                <div onClick={() => setDisplay(!display)}>
                    <CloseIcon/>
                </div>
            </div>
            <>
            {
                    allusers.isFetched ? (
                        <div className='all-users-bottom'>
                            {
                                allusers.data.map((item, index) => (
                                    <UserCard
                                        numb = {getRandomInt(18)}
                                        name = {item.name.first + item.name.last}
                                        img = {item.picture.medium}
                                        username = {item.login.username}
                                    />
                                ))
                            }
                        </div>
                    ) : <div className="loader-wrapper">    
                            <div className="loader"></div>
                        </div>
                }
            </>
        </div>
    )
}

export default AllUsers
