import React from 'react'
import './connection.scss'
import { Link } from 'react-router-dom'

function Connection({img, title,numb,link}) {
    return (
        <Link to={link} className='connection'>
            <div className="connection-left">
                {img}
                <h4 className="connection-left__title">{title}</h4>
            </div>
            <h4 className="connection__title">
                {numb}
            </h4>
        </Link>
    )
}

export default Connection
